/* global document */
import Element from './Element';
import VideoPlayer from './VideoPlayer';

const Layout = (layout_object) => {
    const that = {};
    that.layout_container = document.getElementById(layout_object.elementID);

    that.addPlayer = (player_object) => {
        var layout_inner = Element.getById('inner_'+player_object.elementID);

        if(player_object.remoteStream){
        var elementID = 'player_container_'+player_object.remoteStream.getID();
        var playerID = player_object.remoteStream.getID();
        var playerStream = player_object.remoteStream;
        var playerOptions = player_object.options.playerOptions || {};

        var player = VideoPlayer({
            id: playerID,
            stream: playerStream,
            elementID: player_object.playerID,
            options:playerOptions
        });
        }
        return player;
    }

    that.draw = () => {
        if(that.layout_container){
          var layout_inner = Element({name:'div', id:'inner_'+layout_object.elementID});
          Element.append(layout_inner, that.layout_container);
          if(layout_object.options){
              if(layout_object.options.mode && layout_object.options.mode === 'single') {
                  var playerDiv = Element({name:'div', id:'player_container_'+layout_object.remoteStreams[0].getID()});
                  Element.append(playerDiv, layout_inner);
                  var player_object = {
                      "remoteStream":layout_object.remoteStreams[0],
                      "elementID" : layout_object.elementID,
                      "options" : layout_object.options,
                      "playerID": 'player_container_'+layout_object.remoteStreams[0].getID()
                  };
                  playerDiv = that.addPlayer(player_object);

              }
              else if(layout_object.options.mode && layout_object.options.mode === 'dual') {
                  for(var i = 0; i < 2; i++) {
                      var playerDiv = Element({name:'div', id:'player_container_'+layout_object.remoteStreams[i].getID()});
                      playerDiv.style['float'] = 'left';
                      Element.append(playerDiv, layout_inner);
                      var player_object = {
                          "remoteStream":layout_object.remoteStreams[i],
                          "elementID" : layout_object.elementID,
                          "options" : layout_object.options,
                          "playerID": 'player_container_'+layout_object.remoteStreams[i].getID()
                      };
                      playerDiv = that.addPlayer(player_object);

                  }
              }

          }
      }else{
      }
    }

    return that;
}
export default Layout;



