/* global document */
import Element from './Element';
import Play from './component/Play';
import Zoomer from './component/Zoomer';
import Recorder from "./component/Recorder";
import Mic from "./component/microphone";
import Share from './component/Share';
/*
 * AudioPlayer represents a Audio component that shows either a local or a remote Audio.
 * Ex.: var player = AudioPlayer({id: id, stream: stream, elementID: elementID});
 * A AudioPlayer is also a View component.
 */

const Player = {};
Player.resize = (DOM,height,width) => {
  var DOMP = DOM.parentNode;
  DOMP.style.height = height;
  DOMP.style.width = width;
}
Player.Btn = {};
Player.Btn.apply = function(stream){
    var subbar_id= 'subbar_'+stream.getID();
    if(document.getElementById(subbar_id)!=null){
    var playBtn = document.getElementById(subbar_id).getElementsByClassName('icon_play')[0];
    var zoomBtn = document.getElementById(subbar_id).getElementsByClassName('icon_zoom')[0];
    var recBtn = document.getElementById(subbar_id).getElementsByClassName('icon_record')[0];
    var micBtn = document.getElementById(subbar_id).getElementsByClassName('icon_mic')[0];
    var shareBtn = document.getElementById(subbar_id).getElementsByClassName('icon_share')[0];
    playBtn.onclick = function(e){Play.click(e);};
    zoomBtn.onclick = function(e){
        const skin = e.currentTarget.parentNode.parentNode.parentNode.getAttribute('skin');
        Zoomer.click(e,skin);
    };
    recBtn.onclick = function(e){Recorder.click(e,stream);};
    micBtn.onclick = function(e){Mic.click(e,stream);};
    shareBtn.onclick = function(e){Share.click(e,stream);};
    }
};
Player.setBackgroundImage = (DOM,imageUrl) => {
    var elem =  Element.getByClass('vcx_player_screen_saver',DOM);
    elem.src = imageUrl;
}
Player.addOverlay = (DOM,data) => {
    if(data.elementID && data.options && data.options.size){
        var style_obj = {};
        var sizeArray = data.options.size.split(",");
        style_obj['height']=sizeArray[1];
        style_obj['width']=sizeArray[0];
        style_obj['position'] = 'absolute';
        style_obj['z-index'] = '2';
        if(data.options.coordinates){
            var coor = data.options.coordinates.split(",");
            style_obj['top'] = coor[0];
            style_obj['left'] = coor[1];
            style_obj['bottom'] = coor[2];
            style_obj['right'] = coor[3];
        }
        var className = '';
        if(data.options.class){
            className = data.options.class;
        }
        if(data.options.background_opacity){
            style_obj['background-color'] = 'rgba(255,255,255,'+data.options.background_opacity+')';
        }
        var overlay = Element({name:'div',id:data.elementID,class:className,style:style_obj});
        DOM.appendChild(overlay);
    }else{
    }
}
Player.Overlay={};
Player.Overlay.addContent = (DOM,content) =>{
    DOM.innerHTML = content;
}
Player.Overlay.remove = (DOM) =>{
    DOM.remove();
}
Player.setToolbarDisplayMode = (DOM,mode,displayTimeout) => {
    var elem =  Element.getByClass('vcx_subbar',DOM);
    if(mode){
        if(mode == 'auto'){
            DOM.onmouseover = function(event){
                setTimeout( function(){elem.style.display = "block";},(parseInt(displayTimeout)*1000));
            };
            DOM.onmouseout = function(event){
                setTimeout(function(){ elem.style.display = "none";},(parseInt(displayTimeout)*1000));
            };
        }
        else{
            elem.style.display = "block";
            DOM.onmouseover = function(event){};
            DOM.onmouseout = function(event){};
        }

    }else{
       elem.style.display = "none";
    }
}
export default Player;



